.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


ul.blog-list {
  list-style-type: none;
  padding-left: 0;
}

.blog-list li {
  margin-bottom: 1rem;
}

.date {
  font-weight: bold;
  color: #999; /* Grey color for dates */
}

a {
  color: #ffffff; /* White color for the blog link */
  text-decoration: none;
  font-weight: bold;
}

a:hover {
  text-decoration: underline;
}

